*, *::before, *::after {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

html, body {
    overflow-x: hidden;
}

body {
    font-family: Poppins,sans-serif;
    font-size: 1rem;
    line-height: 1.42857143;
    letter-spacing: .4px;
}

a {
    text-decoration: none;
}
